import {
  Facebook,
  Google,
  Visibility,
  VisibilityOff,
} from "@mui/icons-material";
import {
  Box,
  Button,
  createTheme,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Link,
  styled,
  Tab,
  Tabs,
  TextField,
  Typography,
} from "@mui/material";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
  setPersistence,
  browserSessionPersistence,
} from "firebase/auth";
import { auth } from "../firebase-config.js";
import img from "../assets/shee.jpg";
import React from "react";
import AuthenticationContext from "../Helper/AuthenticationContext.jsx";

const MainLoginDiv = styled("div")(({ theme }) => ({
  padding: 0,
  position: "relative",
  height: "calc(100vh - 100px)",
  width: "100%",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: `url("${img}")`,
  backgroundSize: "cover",
  "@media (max-width: 650px)": {
    flexDirection: "column",
    height: "calc(100vh - 120px)",
  },
}));

const Login = () => {
  const { loggedIn, setLoggedIn } = React.useContext(AuthenticationContext);
  const theme = createTheme();
  const { primary, secondary, success, warning } = theme.palette;
  const [values, setValues] = React.useState({
    username: "",
    password: "",
    regusername: "",
    regpassword: "",
    weight: "",
    weightRange: "",
    showPassword: false,
  });
  const [errorFirebaseLogin, setErrorFirebaseLogin] = React.useState("");
  const [errorFirebaseRegister, setErrorFirebaseRegister] = React.useState("");
  const [user, setUser] = React.useState({});
  onAuthStateChanged(auth, (currentUser) => {
    setUser(currentUser);
    setLoggedIn(currentUser === null ? "Login" : currentUser?.email);
  });
  const signoutUser = async () => {
    await signOut(auth);
    setLoggedIn("Login");
  };
  const loginUser = async () => {
    try {
      const user = await signInWithEmailAndPassword(
        auth,
        values.username,
        values.password
      );
      console.log("Login successfull!");
      setLoggedIn(values.username);
      setValues({ username: "", password: "", weight: "", weightRange: "" });
      setErrorFirebaseLogin("");
    } catch (error) {
      setErrorFirebaseLogin(error.message);
      console.log(error.message);
    }
  };
  const signinUser = async () => {
    try {
      const user = await createUserWithEmailAndPassword(
        auth,
        values.regusername,
        values.regpassword
      );
      console.log("Login successfull!");
      setLoggedIn(values.username);
      setValues({ username: "", password: "", weight: "", weightRange: "" });
      setErrorFirebaseLogin("");
    } catch (error) {
      setErrorFirebaseLogin(error.message);
      console.log(error.message);
    }
  };
  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [value, setValue] = React.useState(0);

  const handleChangeTabVal = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <Tabs value={value} onChange={handleChangeTabVal} variant="fullWidth">
        <Tab label="Login!!" />
        <Tab label="Create an Account" />
      </Tabs>
      <MainLoginDiv>
        {value === 0 ? (
          <Box
            sx={{
              padding: 5,
              height: "60vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              border: "3px solid",
              borderRadius: 2,
              borderTopColor: primary.main,
              borderBottomColor: secondary.main,
              borderLeftColor: success.main,
              borderRightColor: warning.main,
              backdropFilter: "blur(4px)",
              "@media (max-width: 650px)": {
                backdropFilter: "blur(5px)",
              },
            }}
          >
            <Typography variant="h2" gutterbutton="true">
              Login
            </Typography>
            <Box>
              <FormControl sx={{ m: 1, width: "25ch" }} variant="standard">
                <TextField
                  id="standard-basic"
                  value={values.username}
                  onChange={handleChange("username")}
                  label="Username"
                  variant="standard"
                  sx={{
                    width: "100%",
                  }}
                />
              </FormControl>
              <FormControl sx={{ m: 1, width: "25ch" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  Password
                </InputLabel>
                <Input
                  id="standard-adornment-password"
                  type={values.showPassword ? "text" : "password"}
                  value={values.password}
                  onChange={handleChange("password")}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {values.showPassword ? (
                          <VisibilityOff />
                        ) : (
                          <Visibility />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            </Box>
            <Typography
              //variant="body2"
              gutterbutton="true"
              sx={{ color: "red", fontSize: "0.8rem" }}
            >
              {errorFirebaseLogin}
            </Typography>
            <Box
              sx={{
                width: "100%",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <Link href="#">Forget Password</Link>
              <Button variant="contained" onClick={signoutUser}>
                LOG OUT!
              </Button>
              <Button variant="contained" onClick={loginUser}>
                LOG IN!
              </Button>
            </Box>
            <Button
              variant="contained"
              size="large"
              sx={{ justifyContent: "space-evenly" }}
            >
              Login With Google
              <Google />
            </Button>
            <Button
              variant="contained"
              size="large"
              sx={{ justifyContent: "space-evenly" }}
            >
              Login With Facebook
              <Facebook />
            </Button>
            <Typography variant="body2">All Rights Reserved. &copy;</Typography>
          </Box>
        ) : (
          <Box
            sx={{
              padding: 5,
              height: "60vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              border: "3px solid",
              borderRadius: 2,
              borderTopColor: primary.main,
              borderBottomColor: secondary.main,
              borderLeftColor: success.main,
              borderRightColor: warning.main,
              backdropFilter: "blur(4px)",
              "@media (max-width: 650px)": {
                backdropFilter: "blur(5px)",
              },
            }}
          >
            <Typography variant="h2" gutterbutton="true">
              Create an Account
            </Typography>
            <Box>
              <FormControl sx={{ m: 1, width: "25ch" }} variant="standard">
                <TextField
                  id="standard-basic"
                  value={values.regusername}
                  onChange={handleChange("regusername")}
                  label="Username"
                  variant="standard"
                  sx={{
                    width: "100%",
                  }}
                />
              </FormControl>
              <FormControl sx={{ m: 1, width: "25ch" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  Password
                </InputLabel>
                <Input
                  id="standard-adornment-password"
                  type={values.showPassword ? "text" : "password"}
                  value={values.regpassword}
                  onChange={handleChange("regpassword")}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {values.showPassword ? (
                          <VisibilityOff />
                        ) : (
                          <Visibility />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
              <FormControl sx={{ m: 1, width: "25ch" }} variant="standard">
                <InputLabel htmlFor="standard-adornment-password">
                  Re-type Password
                </InputLabel>
                <Input
                  id="standard-adornment-password"
                  type={values.showPassword ? "text" : "password"}
                  //value={}
                  onChange={handleChange("regpassword")}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {values.showPassword ? (
                          <VisibilityOff />
                        ) : (
                          <Visibility />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            </Box>
            <Typography
              //variant="body2"
              gutterbutton="true"
              sx={{ color: "red", fontSize: "0.8rem" }}
            >
              {errorFirebaseLogin}
            </Typography>
            <Box
              sx={{
                width: "100%",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <Button
                variant="contained"
                onClick={signinUser}
                size="large"
                sx={{ width: "100%" }}
              >
                SIGN IN!
              </Button>
            </Box>
            <Typography variant="body2">All Rights Reserved. &copy;</Typography>
          </Box>
        )}
      </MainLoginDiv>
    </>
  );
};

export default Login;
